<template>
  <div class="searchkeywords">
    <div class="search-class">
      <van-empty image="search" description="没找到哦，您可以搜索别的" v-show="show" />
      <div class="product-details" v-for="(item,index) in list" :key="index">
        <div class="product-img">
          <img :src="item.url" @click="adds(index)" alt />
        </div>
        <div class="product-title">
          <img src="../../assets/images/618.png"  alt />
          {{item.title}}
        </div>
        <div class="price_line">
          <div class="price">
            <div>￥</div>
            <span class="int">{{item.price}}</span>
          </div>
          <div class="num_wrap">
            <van-button type="danger" size="small" class="btn-del" @click="adds(index)">立即购买</van-button>
          </div>
        </div>
      </div>
    </div>
    <van-divider>可能你还想要</van-divider>

    <hx-Recommended></hx-Recommended>
  </div>
</template>

<script>
import hxRecommended from "../../components/hxRecommended";

export default {
  data() {
    return {
      show:false,
      list: [
        // {
        //   url:require("../assets/images/hometuijian01.jpg"),
        //   title:"",
        //   price:3999,
        //   id:"20200530",
        // }
      ],
      value: ""
    };
  },
  methods:{
    adds(index){
      this.$store.commit("addfootprint",this.list[index])
        // console.log(this.list[index])
        this.$router.push({name:"productdetails",query:this.list[index]})
    },
  },
  created() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    let list = this.$store.state.list;
    this.value = this.$route.query;
    this.$store.commit("search/add", this.value);
    this.list = list.filter(
      (value, index) => value.title.indexOf(this.value) != -1
    );
    // console.log(this.list)
    if(this.list.length<1){
      this.show = true
    }
  },
  watch: {
    $route(to) {
      // console.log(to.query);
      let list = this.$store.state.list;
      this.value = to.query;
      this.$store.commit("search/add", this.value);
      this.list = list.filter(
        (value, index) => value.title.indexOf(this.value) != -1
      );
      if(this.list.length<1){
      this.show = true
    }
    }
  },
  components: {
    hxRecommended
  }
};
</script>

<style lang="less" scoped>
.searchkeywords {
  padding-top: 20px;
  .search-class {
    > .product-details {
      height: 100px;
      width: 100%;
      margin-top: 20px;
      background-color: #ffffff;
      div {
        float: left;
      }
      > .product-img {
        width: 100px;
        height: 100px;
        position: relative;
        > img {
          width: 80px;
          height: 80px;
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          margin: auto;
        }
      }
      > .product-title {
        margin: 10px 20px 0 20px;
        width: calc(100% - 140px);
        height: 42px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 15px;
        img {
          width: 31px;
          height: 13px;
          margin-right: 10px;
        }
      }
      > .price_line {
        margin: 10px 20px 0 20px;
        width: calc(100% - 140px);
        > .price {
          color: red;
          font-size: 12px;
          > div {
            position: relative;
            top: 5px;
          }
          > .int {
            font-size: 18px;
          }
        }
        > .num_wrap {
          float: right;
        }
      }
    }
  }
}
</style>